html,body{
    background-color: #F3F4F6;
}
.Menu{
    width: 40%;
    margin: 8rem auto;
    border: 2px solid #edecec;
    background-color: #fff;
    border-radius: 7px;
}
.Menu p{
    border-bottom: 1px solid lightgray;
    padding: 15px;
    margin: 0;
}
.Menu a{
    color: #4F46E5;
    font-weight: 600;
    text-decoration: none;
    font-size: 18px;
}
.Menu span{
    float: right;
    background-color: #DCFCE7;
    color: #166534;
    font-size: 12px;
    padding: 5px;
    border-radius: 10px;
    font-weight: 600;
}
.Menu .tag{
    background-color: #FEE2E2;
    color: #991B1B;
}
.go-back{
    position: relative;
    top: 110px;
    text-align: center;
    left: -240px;
}
.go-back a{
    text-decoration: none;
    color: #000000;
}